/* Card Group Container */
.card-group {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* Center cards horizontally */
    gap: 1rem; /* Space between cards */
    padding: 5rem 1.7rem; /* Padding around the card group */
}

/* Styles for individual cards */
.card {
    border-radius: 0.375rem; /* Rounded corners */
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Light shadow */
    transition: all 0.3s ease;
    flex: 1 1 calc(50% - 2rem); /* Flex-grow, flex-shrink, and base size */
    min-width: 250px; /* Minimum width to ensure cards don't shrink too much */
    max-width: 45%; /* Maximum width */
    min-height: 250px; /* Minimum height */
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* Evenly distribute content vertically */
    text-align: center; /* Center text */
    margin-top: 2rem;
}

/* Card Image */
.card-img-top {
    width: 100%;
    padding: 1rem;
    height: auto; /* Adjust height based on the content */
    max-height:32rem;
    min-height: 32rem;
    object-fit: cover; /* Ensure the image covers the entire area */
}

/* Card Body */
.card-body {
    padding: 1rem;
    background-color: inherit; /* Inherit background color based on mode */
    color: inherit; /* Inherit text color based on mode */
}

/* Card title and text */
.card-title {
    font-size: 1.25rem;
    font-weight: 500;
}

.card-text {
    font-size: 1rem;
    overflow: hidden; /* Ensure text doesn't overflow */
    text-overflow: ellipsis; /* Add ellipsis for overflowed text */
    max-height: 10rem; /* Limit height of text area */
    padding: 0 1rem; /* Add padding to align text from left and right */
    box-sizing: border-box; /* Ensure padding is included in the width and height */
    text-align: justify; /* Distribute text evenly across the line */
}

/* Light mode styles */
.light-mode .card {
    background-color: #ffffff; /* Light background */
    color: #333; /* Dark text */
}

.light-mode .card-body {
    background-color: #f8f9fa; /* Light background for card body */
}

.light-mode .text-body-secondary {
    color: #6c757d; /* Light text color for secondary text */
}

/* Dark mode styles */
.dark-mode .card {
    background-color: #343a40; /* Dark background */
    color: #f8f9fa; /* Light text */
}

.dark-mode .card-body {
    background-color: #495057; /* Darker background for card body */
}

.dark-mode .text-body-secondary {
    color: #adb5bd; /* Light grey text color for secondary text */
}

/* Hover effect */
.card:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Darker shadow on hover */
    transform: translateY(-0.5rem); /* Slight lift effect */
}

/* Ensure the card group is centered vertically in the viewport */
body {
    margin: 0; /* Remove default margin */
}
