/* LoginForm.css */
.login-form {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 16px;
    margin-top: 24px;
  }
  
  .login-form-label {
    width: 100%;
  }
  
  .login-form-label-relative {
    width: 100%;
    position: relative;
  }
  
  .login-form-text {
    font-size: 0.875rem;
    color: #000000;
    margin-bottom: 4px;
    line-height: 1.375rem;
  }
  
  .login-form-input {
    background-color: #f7fafc;
    border-radius: 12px;
    width: 100%;
    padding: 12px;
    color: #000000;
  }
  
  .password-eye-icon {
    position: absolute;
    right: 12px;
    top: 38px;
    cursor: pointer;
  }
  
  .forgot-password-link {
    font-size: 0.75rem;
    margin-top: 4px;
    color: #000000;
    max-width: max-content;
    margin-left: auto;
  }
  
  .login-button {
    padding: 8px 12px;
    border-radius: 8px;
    margin-top: 24px;
    font-weight: 500;
    color: #000000;
    background-color: #ffd700; /* Yellow color */
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .login-button:hover {
    background-color: #ffc700;
  }
  