/* Root Variables for Light and Dark Modes */
:root {
    --bg-color-light: #f0f0f0;
    --text-color-light: #333;
    --bg-color-dark: #1a1a1a;
    --text-color-dark: #e0e0e0;
    --card-bg-light: #ffffff;
    --card-bg-dark: #2c2c2c;
    --button-bg-dark: #fff;
    --button-hover-dark: #e0e0e0;
    --input-border-color: #ccc;
    --input-focus-border-color: #000;
}

/* Base Styles */
body {
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: var(--bg-color-light);
    color: var(--text-color-light);
    transition: all 0.3s ease-in-out;
}

body.dark-mode {
    background-color: var(--bg-color-dark);
    color: var(--text-color-dark);
}

/* Container */
.name-generator-container5r {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding-top:3rem;
}

/* Card */
.name-generator-card5r {
    background-color: var(--card-bg-light);
    border-radius: 10px;
    padding: 40px;
    max-width: 500px;
    width: 100%;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: background-color 0.3s ease-in-out;
}

body.dark-mode .name-generator-card5r {
    background-color: var(--card-bg-dark);
}

/* Title */
.title5r {
    font-size: 2.5rem;
    margin-bottom: 10px;
    color: var(--button-bg-light);
    text-decoration: underline;
    font-weight: 800;
}

/* Form */
form {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.form-group5r {
    width: 100%;
    margin-bottom: 20px;
    text-align: left;
}

.form-group5r label {
    display: block;
    margin-bottom: 8px;
    font-size: 1.1rem;
    font-weight: 500;
}

.form-group5r input[type="text"] {
    width: 100%;
    padding: 12px 15px;
    font-size: 1rem;
    border: 1px solid var(--input-border-color);
    border-radius: 5px;
    background-color: #fff;
    color: #333;
    transition: border 0.3s ease-in-out;
}

.form-group5r input[type="text"]:focus {
    border-color: var(--input-focus-border-color);
    outline: none;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.gender-options5r {
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
}

.gender-options5r label {
    font-size: 1rem;
    cursor: pointer;
}

.gender-options5r input {
    margin-right: 8px;
}

/* Buttons */
.button-group5r {
    display: flex;
    justify-content: space-between;
    width: 100%;
    gap: 20px;
}

.button-group5r button {
    flex: 1;
    padding: 12px 20px;
    font-size: 1rem;
    font-weight: bold;
    color: black;
    background-color:white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
}

.button-group5r button:hover {
    border: #000;
}

body.dark-mode .button-group5r button {
    color: #000;
    background-color: var(--button-bg-dark);
}

body.dark-mode .button-group5r button:hover {
    background-color: var(--button-hover-dark);
}

/* Result */
.result5r {
    margin-top: 30px;
    padding: 20px;
    border: 2px dashed var(--button-bg-light);
    border-radius: 10px;
    animation: fadeIn5r 0.5s ease-in-out;
}

.result5r h2 {
    margin-bottom: 10px;
    font-size: 1.5rem;
    color: var(--text-color-light);
}

.result5r p {
    font-size: 1.2rem;
    color: var(--text-color-light);
}

.result5r .generated-name5r {
    font-size: 2.5rem;
    color: var(--button-bg-light);
    margin-top: 10px;
}

body.dark-mode .result5r {
    border-color: var(--button-bg-dark);
}

body.dark-mode .result5r h2,
body.dark-mode .result5r p,
body.dark-mode .result5r .generated-name5r {
    color: var(--text-color-dark);
}

/* Animated Text */
.animated-text5r {
    margin-top: 40px;
    font-size: 1.3rem;
    font-weight: bold;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    height: 30px;
    width: 100%;
    text-align: left;
    white-space: nowrap;
    color: var(--text-color-light);
}

body.dark-mode .animated-text5r {
    color: var(--text-color-dark);
}

.animated-text5r span {
    animation: slideInOut5r 3s infinite;
}

/* Keyframes */
@keyframes slideInOut5r {
    0%, 100% {
        transform: translateX(100%);
        opacity: 0;
    }
    10%, 90% {
        transform: translateX(0%);
        opacity: 1;
    }
}

@keyframes fadeIn5r {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
