/* Template.css */
.template-container {
    display: flex;
    width: 100%;
    max-width: 100%;
    padding: 80px 32px 24px;
    margin: 0 auto;
    justify-content: space-between;
    background-color: #f7fafc;
    overflow-x: hidden;
    overflow-y: auto;
  }
  
  .template-text-section {
    width: 91.6667%;
    max-width: 450px;
    margin: 0;
  }
  
  .template-title {
    font-weight: 600;
    font-size: 1.875rem;
    line-height: 2.375rem;
    color: #1a202c;
  }
  
  .template-description {
    font-size: 1.125rem;
    margin-top: 16px;
    line-height: 1.625rem;
  }
  
  .template-desc1 {
    color: #4a5568;
  }
  
  .template-desc2 {
    color: #3182ce;
    font-style: italic;
  }
  
  .template-form-container {
    display: flex;
    width: 100%;
    align-items: center;
    margin: 16px 0;
    gap: 8px;
  }
  
  .template-divider {
    height: 1px;
    width: 100%;
    background-color: #cbd5e0;
  }
  
  .template-or-text {
    color: #718096;
    font-weight: 500;
    line-height: 1.375rem;
  }
  
  .template-google-button {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    font-weight: 500;
    color: #1a202c;
    border: 1px solid #cbd5e0;
    padding: 8px 12px;
    margin-top: 24px;
    background-color: #ffffff;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .template-google-button:hover {
    background-color: #edf2f7;
  }
  
  .template-image-section {
    position: relative;
    width: 91.6667%;
    max-width: 450px;
  }
  
  .template-main-image {
    position: absolute;
    top: -16px;
    right: 16px;
  }
  