@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  display: flex;
  background: #333;
  justify-content: flex-end;
  align-items: flex-end;
  min-height: 100vh;
}

.footer {
  position: relative;
  width: 100%;
  min-height: 6.25rem; /* 100px */
  /* padding: 1.25rem 3.125rem; 20px 50px */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

/* Common styles for social icons and menu */
.social-icon,.copy,
.menu {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.social-icon__item,
.menu__item {
  list-style: none;
}

.social-icon__link {
  font-size: 2rem; /* 32px */
  margin: 0 0.625rem; /* 10px */
  display: inline-block;
  transition: 0.5s;
}

.social-icon__link:hover {
  transform: translateY(-0.625rem); /* -10px */
}

.menu__link {
  font-size: 1.5rem; /* 24px */
  margin: 0 0.625rem; /* 10px */
  display: inline-block;
  transition: 0.5s;
  text-decoration: none;
  opacity: 0.75;
  font-weight: 300;
}

.menu__link:hover {
  opacity: 1;
}

.footer p {
  /* margin: 0.9375rem 0 0.625rem 0; 15px 10px */
  font-size: 1.125rem; /* 18px */
  font-weight: 300;
}

/* Dark Mode Styles */
.dark-mode {
  background-color: #0a0a0a; /* richblack-900 */
}

.dark-mode .social-icon__link {
  color: #f5f5f5; /* Light text color for dark background */
}

.dark-mode .menu__link {
  color: #f5f5f5; /* Light text color for dark background */
}

.dark-mode .footer p {
  color: #f5f5f5; /* Light text color for dark background */
}

.dark-mode .wave {
  background-color: var(--wave-dark); /* Dark color for wave */
}

.dark-mode #wave1,
.dark-mode #wave2,
.dark-mode #wave3,
.dark-mode #wave4 {
  background: url("https://i.ibb.co/wQZVxxk/wave-dark.png"); /* Dark mode wave image */
}

/* Light Mode Styles */
.light-mode .copy {
  background-color: #e5e7ec; /* bg-gray-200 */
}

.light-mode .social-icon__link {
  color: #333; /* Dark text color for light background */
}

.light-mode .menu__link {
  color: #333; /* Dark text color for light background */
}

.light-mode .footer p {
  color: #333; /* Dark text color for light background */
}

.light-mode .wave {
  background-color: var(--wave-light); /* Light color for wave */
}

.light-mode #wave1,
.light-mode #wave2,
.light-mode #wave3,
.light-mode #wave4 {
  background: url("https://i.ibb.co/wQZVxxk/wave-light.png"); /* Light mode wave image */
}

/* Base styles for waves */
.wave {
  position: absolute;
  top: -0.0025rem; /* -50px (Half of -100px) */
  left: 0;
  width: 100%;
  height: .125rem; /* 50px (Half of 100px) */
  background-size: 31.25rem 3.125rem; /* 500px 50px (Half of 1000px 100px) */
  background-repeat: repeat-x; /* Ensure background repeats horizontally */
}

/* Wave Animations */
.wave#wave1 {
  z-index: 90;
  opacity: 1;
  bottom: 0;
  animation: animateWaves 4s linear infinite;
}

.wave#wave2 {
  z-index: 999;
  opacity: 0.5;
  bottom: 0.3125rem; /* 5px (Half of 10px) */
  animation: animate 4s linear infinite !important;
}

.wave#wave3 {
  z-index: 1000;
  opacity: 0.2;
  bottom: 0.46875rem; /* 7.5px (Half of 15px) */
  animation: animateWaves 3s linear infinite;
}

.wave#wave4 {
  z-index: 999;
  opacity: 0.7;
  bottom: 0.625rem; /* 10px (Half of 20px) */
  animation: animate 3s linear infinite;
}

@keyframes animateWaves {
  0% {
    background-position-x: 31.25rem; /* 500px (Half of 1000px) */
  }
  100% {
    background-position-x: 0;
  }
}

@keyframes animate {
  0% {
    background-position-x: -31.25rem; /* -500px (Half of -1000px) */
  }
  100% {
    background-position-x: 0;
  }
}

/* CSS Variables for wave colors */
:root {
  --wave-dark: #493f3f; /* Example dark color, adjust as needed */
  --wave-light: #f0f0f0; /* Example light color, adjust as needed */
}
