:root {
    --primary-color: #e74c3c;
    --secondary-color: #3498db;
    --text-color: #333;
    --bg-color: #f4f4f4;
    --card-bg: #ffffff;
    --input-border: #ddd;
    --input-focus: #e74c3c;
  }
  
  body {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
    background-color: var(--bg-color);
    color: var(--text-color);
  }
  
  .match-generator-containeraa {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 20px;
  }
  
  .match-generator-cardaa {
    background-color: var(--card-bg);
    border-radius: 15px;
    padding: 40px;
    max-width: 600px;
    width: 100%;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .titleaa {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: var(--primary-color);
    font-weight: 700;
  }
  
  .form-groupaa {
    margin-bottom: 25px;
    text-align: left;
  }
  
  .form-groupaa label {
    display: block;
    margin-bottom: 8px;
    font-size: 1.1rem;
    font-weight: 600;
    color: var(--text-color);
  }
  
  .form-groupaa input[type="text"],
  .form-groupaa input[type="number"] {
    width: 100%;
    padding: 12px 15px;
    font-size: 1rem;
    border: 2px solid var(--input-border);
    border-radius: 8px;
    transition: border-color 0.3s ease;
  }
  
  .form-groupaa input[type="text"]:focus,
  .form-groupaa input[type="number"]:focus {
    border-color: var(--input-focus);
    outline: none;
  }
  
  .gender-optionsaa {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }
  
  .gender-optionsaa label {
    flex: 1;
    padding: 10px;
    text-align: center;
    background-color: #f0f0f0;
    border: 2px solid var(--input-border);
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .gender-optionsaa input[type="radio"] {
    display: none;
  }
  
  .gender-optionsaa input[type="radio"]:checked + label {
    background-color: var(--primary-color);
    color: white;
  }
  
  .button-groupaa {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }
  
  .button-groupaa button {
    flex: 1;
    padding: 12px 20px;
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: var(--primary-color);
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button-groupaa button:hover {
    background-color: darken(var(--primary-color), 10%);
  }
  
  .button-groupaa button:last-child {
    background-color: var(--secondary-color);
  }
  
  .button-groupaa button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  .resultaa {
    margin-top: 40px;
    padding: 30px;
    border: 2px solid var(--primary-color);
    border-radius: 15px;
    background-color: #f9f9f9;
    animation: fadeInaa 0.5s ease-in-out;
  }
  
  .resultaa h2 {
    color: var(--primary-color);
    margin-bottom: 20px;
  }
  
  .match-infoaa {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .match-photoaa {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 20px;
  }
  
  .match-detailsaa {
    text-align: left;
  }
  
  .match-detailsaa p {
    margin: 10px 0;
  }
  
  .animated-textaa {
    margin-top: 40px;
    font-size: 1.3rem;
    font-weight: bold;
    display: flex;
    justify-content: center;
    overflow: hidden;
    height: 30px;
  }
  
  .animated-textaa span {
    display: inline-block;
    margin: 0 10px;
    animation: slideInOutaa 10s infinite;
    opacity: 0;
  }
  
  @keyframes slideInOutaa {
    0%, 100% { transform: translateY(100%); opacity: 0; }
    10%, 90% { transform: translateY(0); opacity: 1; }
  }
  
  @keyframes fadeInaa {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
  }
  
  @media (max-width: 600px) {
    .match-generator-cardaa {
      padding: 20px;
    }
    
    .titleaa {
      font-size: 2rem;
    }
    
    .button-groupaa {
      flex-direction: column;
    }
    
    .button-groupaa button {
      margin-bottom: 10px;
    }
  
    .match-infoaa {
      flex-direction: column;
    }
  }