/* Base styles for both light and dark mode */
:root {
    --bg-color-light: #f0f0f0;
    --text-color-light: #333;
    --bg-color-dark: #1a1a1a;
    --text-color-dark: #e0e0e0;
    --card-bg-light: #ffffff;
    --card-bg-dark: #2c2c2c;
    /* --button-bg-light: #c9e1fa; */
    --button-bg-dark: #333;
    /* --button-hover-light: #b0d3f9; */
    --button-hover-dark: #444;
}
.title1{
    font-weight: 700;
    text-decoration: underline;
    font-size:2rem ;
}
.font-control{
    display:flex ;
    align-items: center;
    justify-content: center;
    /* outline:2px solid  #1a1a1a; */
}
.crush-input:focus {
    outline: 2px solid #1a1a1a;
}

body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    transition: background-color 0.3s, color 0.3s;    min-height: 99vh;
 

}

body.light-mode {
    --bg-color: var(--bg-color-light);
    --text-color: var(--text-color-light);
}

body.dark-mode {
    --bg-color: var(--bg-color-dark);
    --text-color: var(--text-color-dark);
}

.flamerss-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: var(--bg-color);
    color: var(--text-color);
    padding-top: 20px;
    /* background-color: #e0e0e0; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.flamerss-card {
    background-color: var(--card-bg-light);
    color: var(--text-color);
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 600px;
    text-align: center;
    max-height: 70%;
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175) ;
}

body.dark-mode .flamerss-card {
    background-color: var(--card-bg-dark);
}

.flamerss-text {
    font-size: 1.25rem;
    font-weight: bold;
    text-align: center;
    margin: 20px 0;
}

.flamerss-text p {
    opacity: 0;
    animation: fadeInOut 12s infinite;
}

.flamerss-text p:nth-child(1) {
    animation-delay: 0s;
}

.flamerss-text p:nth-child(2) {
    animation-delay: 2s;
}

.flamerss-text p:nth-child(3) {
    animation-delay: 4s;
}

.flamerss-text p:nth-child(4) {
    animation-delay: 6s;
}

.flamerss-text p:nth-child(5) {
    animation-delay: 8s;
}

.flamerss-text p:nth-child(6) {
    animation-delay: 10s;
}

@keyframes fadeInOut {
    0%, 100% {
        opacity: 0;
    }
    10%, 30% {
        opacity: 1;
    }
}

.animated-text {
    font-size: 1.25rem;
    font-weight: bold;
    text-align: center;
    white-space: nowrap; /* Keeps text on a single line */
}

.animated-text span {
    display: inline-block;
    opacity: 0;
    animation: fadeInOut 12s infinite;
}

.animated-text span:nth-child(1) {
    animation-delay: 0s;
}

.animated-text span:nth-child(2) {
    animation-delay: 2s;
}

.animated-text span:nth-child(3) {
    animation-delay: 4s;
}

.animated-text span:nth-child(4) {
    animation-delay: 6s;
}

.animated-text span:nth-child(5) {
    animation-delay: 8s;
}

.animated-text span:nth-child(6) {
    animation-delay: 10s;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

input {
    margin: 10px;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid var(--text-color);
    border-radius: 4px;
    background-color: var(--bg-color);
    color: var(--text-color);
    width: 100%;
}

button {
    padding: 10px 20px;
    font-size: 1rem;
    color: var(--text-color);
    background-color: var(--button-bg-light);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
}

body.dark-mode button {
    background-color: var(--button-bg-dark);
}

button:hover {
    background-color: var(--button-hover-light);
}

body.dark-mode button:hover {
    background-color: var(--button-hover-dark);
}
