* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* Light Mode Styles */
.light-mode {
  background-color: #f5f5f5; /* Light background color */
  color: #333;
}

.light-mode .App-header {
  background-color: #ffffff; /* Light header background color */
}

.light-mode .App-link {
  color: #007bff; /* Link color for light mode */
}

.light-mode .text-richblack-100 {
  color: #333; /* Text color for light mode */
}

/* Dark Mode Styles */
.dark-mode {
  background-color: #1f2937; /* Dark background color */
  color: #fff;
}

.dark-mode .App-header {
  background-color: #282c34; /* Dark header background color */
}

.dark-mode .App-link {
  color: #61dafb; /* Link color for dark mode */
}

.dark-mode .text-richblack-100 {
  color: #f5f5f5; /* Text color for dark mode */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
