/* Navbar.css */
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    background-color: var(--navbar-bg-color);
    color: var(--navbar-text-color);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 10;
  }
  
  .navbar a {
    color: var(--navbar-link-color);
    text-decoration: none;
    margin-left: 20px;
    transition: color 0.3s ease;
  }
  
  .navbar a:hover {
    color: var(--navbar-link-hover-color);
  }
  
  .navbar button {
    padding: 10px;
    background: none;
    border: none;
    cursor: pointer;
    color: var(--navbar-link-color);
    transition: color 0.3s ease;
  }
  
  .navbar button:hover {
    color: var(--navbar-link-hover-color);
  }
  
  .navbar .left-links {
    display: flex;
    align-items: center;
  }
  
  .navbar .right-links {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  
  /* Dark and Light Mode Variables */
  :root {
    --navbar-bg-color: #f0f0f0;
    --navbar-text-color: #000;
    --navbar-link-color: #000;
    --navbar-link-hover-color: #555;
  }
  
  :root.dark-mode {
    --navbar-bg-color: #0f0f0f;
    --navbar-text-color: #fff;
    --navbar-link-color: #fff;
    --navbar-link-hover-color: #aaa;
  }
  